import React, { useState, useRef} from 'react';
import './practice.scss';
import { BrowserRouter as Router, Switch, Route, Link, NavLink, Redirect } from 'react-router-dom';
import { useEffect } from 'react';



export default Practice;
function Practice() {
  return (
    <div>
      <h1>
        This is React Router Practice
      </h1>
      <Router>
        <Switch>
          <Route path="/" exact> <Home></Home> </Route>
          <Route path="/about"> <About> </About></Route>
          <Route path="/page"><Page></Page></Route>
          <Route path="/contact"> <AllContact></AllContact> </Route>
          <Route path="/contact:id"> <Contact1></Contact1></Route>
          <Route path="/contact:id"> <Contact2></Contact2></Route>
          <Route path="/blog"> <Blog></Blog> </Route>
          <Route> <NotFound></NotFound> </Route>
        </Switch>
      </Router>

    </div>
  )
}
const Home = () => {
  const [ready, setReady] = useState('');
  const redi = useRef();
  function btnReady(){
  console.log(redi)
  }
  return (
    <ul>
      <h1>This is home page</h1>
      <Router>
        <li><NavLink activeClassName="hareKrishna" exact target="_blank" to="/">Home</NavLink></li>
        <li><NavLink activeClassName="hareKrishna" target="_blank" to="/about">About</NavLink></li>
        <li><NavLink activeClassName="hareKrishna" target="_blank" to="/pages">Page</NavLink></li>
        <li><NavLink activeClassName="hareKrishna" target="_blank" to="/contact">Contact</NavLink></li>
        <li><NavLink  ref={redi} activeClassName="hareKrishna" target="_blank" to="/blog">Blog</NavLink></li>
        <li><Redirect ref={redi} to=""/>
        <button onClick={ btnReady}> Click To Redirect</button>
        </li>
      </Router>
    </ul>
  )
}


const About = e=> {
  return(
    <Router>
    <li><NavLink activeClassName="hareKrishna" target="_blank" to="/">Home</NavLink></li>
    <li><NavLink activeClassName="hareKrishna" target="_blank" to="/about">About</NavLink></li>
    <li><NavLink activeClassName="hareKrishna" target="_blank" to="/pages">Page</NavLink></li>
    <li><NavLink activeClassName="hareKrishna" target="_blank" to="/contact">Contact</NavLink></li>
    <li><NavLink activeClassName="hareKrishna" target="_blank" to="/blog">Blog</NavLink></li>
    <li><NavLink activeClassName="hareKrishna" target="_blank" to="/contact:1">Contact 1</NavLink></li>
    <li><NavLink activeClassName="hareKrishna" target="_blank" to="/contact:2">Contact 2</NavLink></li>
    
  </Router>
  )
}
const Page = e => <h1> This is page of page</h1>
const Contact1 = e => <h1>This is contact page 1</h1>
const Contact2 = e => <h1>This is contact page 2</h1>
const Blog = e => <h1>This is blog page</h1>
const NotFound = e=> <h1>This is NotFound 404 page</h1>
const AllContact = e=> <h1>This is all contact</h1>